<template>
  <div class="page">
    <div class="page__content">
      <div class="page__header">
        <h1 class="page__title">
          История просмотров
        </h1>
      </div>
      <div class="history-page">
        <div
          class="history-page__group-day"
          v-for="(days, index) in cards"
          :key="index"
        >
          <h2 class="history-page__title">
            {{ index }}
          </h2>
          <div class="history-page__cards">
            <div
              class="history-page__group-cards"
              v-for="(card, cardIndex) in days"
              :key="cardIndex"
            >
              <news-card
                :news="card.entityBody"
                v-if="card.entity_type === 'post' && card.entityBody"
                :id="card.entityBody.id"
              />
              <art-startup-card
                :art-startup="card.entityBody"
                :id="card.entityBody.id"
                v-if="card.entity_type === 'startup' && card.entityBody"
              />
              <event-card
                :event="card.entityBody"
                :id="card.entityBody.id"
                v-if="card.entity_type === 'event' && card.entityBody"
              />
            </div>
          </div>
        </div>
        <button
          class="playbill__more-btn button-warning"
          v-show="showMore"
          @click="loadMore"
        >
          показать ещё
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import mixin from "../mixin/mixin";
import axios from "axios";
import NewsCard from "../components/Cards/NewsCard";
import ArtStartupCard from "../components/Cards/ArtStartupCard";
import EventCard from "../components/Cards/EventCard";

export default {
  name: "HistoryPage",
  components: { EventCard, ArtStartupCard, NewsCard },
  mixins: [mixin],
  data() {
    return {
      cards: {},
      loadArray: [],
      pageData: [],
      currentPage: 1,
      perPage: 20
    };
  },
  computed: {
    showMore() {
      return this.pageData.pageCount > this.pageData.currentPage;
    }
  },
  mounted() {
    this.loadData();
  },
  methods: {
    async loadData() {
      let load = this.$loading.show();

      let url = new URL(`${process.env.VUE_APP_API_URL}/v1/view-history/list`);
      url.searchParams.set("per-page", this.perPage);
      url.searchParams.set("page", this.currentPage);

      await axios
        .get(url)
        .then(response => {
          if (this.currentPage > 1) {
            this.loadArray.push(...response.data._items);
          } else {
            this.loadArray = response.data._items;
          }

          this.cards = this.groupByDate(this.loadArray);
          this.pageData = response.data._meta;
          load.hide();
        })
        .catch(error => {
          if (!error.response) {
            this.$toasted.global.error("Нет соединения с сервером");
            load.hide();
          }
          this.$toasted.global.error(error.response.data.message);
          load.hide();
        });
    },
    loadMore() {
      if (this.pageData.pageCount > this.currentPage) {
        this.currentPage++;
      }
      this.loadData();
    },

    groupByDate(array) {
      let key = null;
      return array.reduce((res, item) => {
        key = this.$moment(item.created_at * 1000)
          .startOf("day")
          .calendar(null, {
            sameDay: "[Сегодня]",
            nextDay: "[Завтра]",
            nextWeek: "DD MMMM YYYY",
            lastDay: "[Вчера]",
            lastWeek: "DD MMMM YYYY",
            sameElse: "DD MMMM YYYY"
          });

        res[key] = res[key] || [];
        res[key].push(item);
        return res;
      }, {});
    }
  }
};
</script>
