<template>
  <div @click.stop="$router.push(`/event/${id}`)" class="event-card">
    <div class="event-card__img">
      <div class="event-card__top-panel" v-show="review">
        <div class="event-card__review">
          <svg>
            <use href="@/assets/img/sprite.svg#icon-lock"></use>
          </svg>
          На проверке
        </div>
      </div>
      <div
        class="event-card__edit"
        v-show="edit"
        @click.stop="$router.push(`/update-event/${id}`)"
      >
        <svg class="icon-user-residents">
          <use href="@/assets/img/sprite.svg#icon-user-residents"></use>
        </svg>
        <svg class="icon-edit">
          <use href="@/assets/img/sprite.svg#icon-edit"></use>
        </svg>
      </div>
      <img
        :src="event.title_picture.url"
        loading="lazy"
        alt="photo"
        height="150"
      />
    </div>
    <div class="event-card__wrap">
      <div
        class="event-card__bookmarked"
        :class="{ 'animation-bookmarked': event.is_favorite }"
        v-if="event.is_favorite && isAuthenticated"
        @click.stop="onDeleteFavorite"
      >
        <svg width="18" height="18">
          <use href="@/assets/img/sprite.svg#icon-bookmark"></use>
        </svg>
      </div>
      <div
        class="event-card__bookmarked"
        v-if="!event.is_favorite && isAuthenticated"
        @click.stop="onCreateFavorite"
      >
        <svg width="18" height="18">
          <use href="@/assets/img/sprite.svg#icon-bookmark-border"></use>
        </svg>
      </div>
      <div class="event-card__genre">{{ getCategories }}</div>
      <div class="event-card__location">{{ getPlace }}</div>
      <div class="event-card__date">{{ getDate }}</div>
      <div class="event-card__name">{{ getTitle }}</div>
      <!--      <div class="event-card__footer">-->
      <!--        <span class="event-card__link">-->
      <!--          {{ event.const | filterCost }}-->
      <!--        </span>-->
      <!--        <router-link to="/tickets" class="event-card__ticket-btn">-->
      <!--          <svg width="24" height="24">-->
      <!--            <use href="@/assets/img/sprite.svg#icon-ticket"></use>-->
      <!--          </svg>-->
      <!--        </router-link>-->
      <!--      </div>-->
    </div>
  </div>
</template>

<script>
import mixin from "../../mixin/mixin";
import { mapGetters } from "vuex";
import qs from "qs";
import axios from "axios";

export default {
  name: "EventCard",
  mixins: [mixin],
  props: {
    event: {
      type: Object,
      default: () => {}
    },
    review: {
      type: Boolean,
      default: false
    },
    id: {
      type: Number,
      default: null
    },
    edit: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    ...mapGetters("auth", ["isAuthenticated"]),
    getTitle() {
      return this.event.title ? this.event.title : "";
    },
    getPlace() {
      return this.event.place && this.event.place.title
        ? this.event.place.title
        : "";
    },
    getDate() {
      return this.event.begin_time
        ? this.$options.filters.formatDate(this.event.begin_time)
        : "";
    },
    getCategories() {
      return this.event.categories.length && this.event.categories[0].title
        ? this.event.categories[0].title
        : "";
    }
  },
  methods: {
    onCreateFavorite() {
      let form = qs.stringify({
        entity_type: "event",
        entity_id: this.event.id
      });
      axios
        .post(`${process.env.VUE_APP_API_URL}/v1/favorite/create`, form)
        .then(response => {
          this.$emit("event-set-favorite", response.data.id);
        })
        .catch(error => {
          if (!error.response) {
            this.$toasted.global.error("Нет соединения с сервером");
          }
          console.log(error);
        });
    },
    onDeleteFavorite() {
      let url = new URL(`${process.env.VUE_APP_API_URL}/v1/favorite/delete`);
      url.searchParams.append("id", this.event.is_favorite);
      axios
        .delete(url)
        .then(() => {
          this.$emit("event-delete-favorite");
        })
        .catch(error => {
          if (!error.response) {
            this.$toasted.global.error("Нет соединения с сервером");
          }
          console.log(error);
        });
    }
  }
};
</script>

<style scoped></style>
