<template>
  <div @click.stop="$router.push(`/art-startup/${id}`)" class="startup-card">
    <div class="startup-card__img">
      <div class="startup-card__top-panel" v-show="review">
        <div class="startup-card__review">
          <svg>
            <use href="@/assets/img/sprite.svg#icon-lock"></use>
          </svg>
          На проверке
        </div>
      </div>
      <div
        class="startup-card__edit"
        v-show="edit"
        @click.stop="$router.push(`/update-startup/${id}`)"
      >
        <svg class="icon-user-residents">
          <use href="@/assets/img/sprite.svg#icon-user-residents"></use>
        </svg>
        <svg class="icon-edit">
          <use href="@/assets/img/sprite.svg#icon-edit"></use>
        </svg>
      </div>
      <img
        v-show="artStartup.title_picture.url"
        :src="artStartup.title_picture.url"
        alt="photo"
        width="260"
        height="150"
      />
    </div>
    <div class="startup-card__wrap">
      <div class="startup-card__name">{{ artStartup.title }}</div>
      <star-rating
        :star-size="20"
        :rating="artStartup.rating"
        :read-only="true"
      ></star-rating>
      <a href="#" class="startup-card__resident">
        {{ artStartup.author.full_name }}
      </a>
    </div>
  </div>
</template>

<script>
import mixin from "../../mixin/mixin";
import StarRating from "vue-star-rating";

export default {
  name: "ArtStartupCard",
  components: { StarRating },
  mixins: [mixin],
  props: {
    artStartup: {
      type: Object,
      default: () => {}
    },
    id: {
      type: Number,
      default: null
    },
    review: {
      type: Boolean,
      default: false
    },
    edit: {
      type: Boolean,
      default: false
    }
  }
};
</script>

<style scoped></style>
