<template>
  <div @click.stop="$router.push(`/news-view/${id}`)" class="news-card">
    <div class="news-card__img">
      <div class="news-card__top-panel" v-show="review">
        <div class="news-card__review">
          <svg>
            <use href="@/assets/img/sprite.svg#icon-lock"></use>
          </svg>
          На проверке
        </div>
      </div>
      <div
        class="news-card__edit"
        v-show="edit"
        @click.stop="$router.push(`/update-news/${id}`)"
      >
        <svg class="icon-user-partner">
          <use href="@/assets/img/sprite.svg#icon-user-partner"></use>
        </svg>
        <svg class="icon-edit">
          <use href="@/assets/img/sprite.svg#icon-edit"></use>
        </svg>
      </div>
      <img :src="news.title_picture.url" alt="photo" width="260" height="150" />
    </div>
    <div class="news-card__wrap">
      <div class="news-card__name">{{ news.title }}</div>
      <div class="news-card__date">
        {{ news.published_at | formatDate }}
      </div>
    </div>
  </div>
</template>

<script>
import mixin from "../../mixin/mixin";

export default {
  name: "NewsCard",
  mixins: [mixin],
  props: {
    news: {
      type: Object,
      default: () => {}
    },
    id: {
      type: Number,
      default: null
    },
    review: {
      type: Boolean,
      default: false
    },
    edit: {
      type: Boolean,
      default: false
    }
  }
};
</script>

<style scoped></style>
